import React from "react";
import styled from "styled-components";
import {Fade} from 'react-reveal';

const About = () => {
  return (
    <Wrapper>
      
      <Heading>
        <h2 className="title">About Me</h2>
        <p className="subTitle">Have a look about me</p>
      </Heading>
      <Fade>
      <MainSection>
        <Image src="https://source.unsplash.com/1600x900/?nature,water" />
        <AboutText>
          <h3 className="title">Hey!</h3>
          <p className="description">
            I am a full stack developer who lives in Faridabad,India. I have a
            year of experience in JavaScript as primary language.
          </p>
          <p className="description">
            I started coding recently during Covid-19 lockdown and found it
            exciting, later got myself enrolled in online course on Udemy( a
            Online learning and teaching marketplace).
          </p>
          <p className="description">
            I started Learning HTML, CSS and JavaScript. Later i picked React JS
            as a front end library and started doing projects. Later on i turned
            into full stack development with MERN.
          </p>
          <p className="description">
            I always look for learning as much as i can with new and interesting
            technologies. Always trying to problem solve and look for new ideas
            into development.
          </p>
          <p className="description">
            However, at the moment i am looking for to work in company to gain
            more experience into Front End / Full Stack development. I really
            want to work with a team, learn, work with clients and make
            applications for companies.
          </p>
          <p className="description">
            My goal in the future is to work and get expert in different
            technologies with time, then start helping others to grow.
          </p>
        </AboutText>
      </MainSection>
      </Fade>
    </Wrapper>
  );
};

export default About;

const Wrapper = styled.div`
  width: 80%;
  margin: 10px auto;
`;
const Heading = styled.div`
  text-align: center;
  padding: 15px 0;
  .title {
    font-size: 30px;
    padding: 10px;
  }
  .subTitle {
    font-size: 18px;
    font-weight: 500;
    color: grey;
  }
`;
const Image = styled.img`
  width: 400px;
  border-radius: 12px;
  object-fit: contain;
`;
const MainSection = styled.div`
  display: flex;
  flex-wrap: wrap;
`;
const AboutText = styled.div`
  width: 50%;
  padding: 0 10px;
  .title {
    font-size: 28px;
  }
  .description {
    padding: 10px 0;
    font-size: 17px;
  }
`;
