import React from 'react';
import styled from 'styled-components';
import {Fade} from 'react-reveal';

const Header = () => {
    return (
        <Wrapper>
            <Fade >
            <LeftSection>
                <p><a href="/"> Avinash Kumar</a></p>
            </LeftSection>
            <RightSection>
                <li>Twitter</li>
                <li>GithHub</li>
                <li>Youtube</li>
            </RightSection>
            </Fade>
        </Wrapper>
    )
}

export default Header

const Wrapper = styled.div`
    display:flex;
    justify-content: space-between;
    padding: 20px 25px;
    
`
const LeftSection = styled.div`
    a{
        text-decoration:none;
        color:inherit;
    }
    font-size:30px;
    font-weight:700;
    color: var(--mainColor);
`
const RightSection = styled.div`
    display:flex;
    align-items: center;
    li{
        list-style:none;
        padding:5px 10px;
    }
`