import React from 'react';
import styled from 'styled-components';
import {projects} from '../Data/projects';
import {Button} from './Button';
import {Fade} from 'react-reveal';

const Projects = () => {
    
    return (
        <Wrapper>
            <Fade cascade>
            <div className="projectIntro">
                <h2 className='projectHeading'>Projects</h2>
                <p className="projectInfo">Have a look at some of my projects.</p>
            </div>
            {projects.map((project,index)=>(
                <ProjectWrapper key={index} >
                    <Image src={project.image} />
                    <Info>
                        <h2 className="title">{project.title}</h2>
                        <p className="description">{project.description}</p>
                        <div className="techUsed">Tech used:{project.techUsed.map((tech,index)=> (<p key={index} className="tech" >{'#'+tech} </p>) ) }</div>
                        <div className="links">
                            <a href="http://github.com" target="_blank" rel="noreferrer" ><Button color='white' bgColor='var(--mainColor)' > GitHub</Button></a>  
                            <a href={project.link.site} target="_blank" rel="noreferrer" ><Button color='var(--mainColor)' bgColor='white' >Website</Button></a> 
                        </div>
                    </Info>
                </ProjectWrapper>
            ) ) }
            </Fade>
        </Wrapper>
    )
}

export default Projects

const Wrapper = styled.div`
    a{
        text-decoration:none;
        color:inherit;
    }
    margin: 8px 0;
    .projectIntro{
        text-align: center;
        margin:15px 0;
        padding:20px;
        
        .projectHeading{
            font-size: 32px;
        }
        .projectInfo{
            font-size: 20px;
            font-weight: 500;
            color: #808080bf;
        }
    }
`
const ProjectWrapper = styled.div`
    display:flex;
    gap:20px 30px;
    /* border:2px solid yellow; */
    margin:18px 0;
    flex-wrap: wrap;
    justify-content: center;
`
const Image = styled.img`
    height:300px;
    border: 4px solid #e85c3073;
    border-radius: 12px;
`
const Info = styled.div`
display:flex;
flex-direction: column;
gap:18px;
width:min-content;
.title{
    font-size: 34px;
}
.description{
    font-size: 20px;
    font-weight: 500;
    color: gray;
}
.techUsed{
    font-weight: 700;
    display:flex;
    gap:2px;
    flex-wrap: wrap;
    font-size: 20px;
    color: #000000d6;
    .tech{
        border-right:2px solid var(--mainColor);
        font-weight: 500;
        color: #524d4d;
        padding: 0 8px;
        }
    }
.links{
    display:flex;
    a{
        border:none;
        margin:8px 12px;
    }
}
`